.homePage {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.homePage__bgText {
  font-size: 8rem;
  rotate: 30deg;
  text-transform: uppercase;
  white-space: nowrap;
  animation: text 6s linear 1s infinite;
  opacity: 0.3;
  z-index: 1;
  position: absolute;
  bottom: -1000px;
}
.homePage__header {
  position: absolute;
  font-size: 7rem;
  z-index: 2;
  text-align: center;
  color: #21b8ed;
}
.homePage__button {
  background-color: #21b8ed;
  width: 300px;
  margin-top: 280px;
  border-radius: 20px;
  color: white;
  z-index: 2;
  font-size: 3rem;
  height: 130px;
  transition: 0.2s;
  overflow: hidden;
  padding-top: 38px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.button__img {
  width: 120px;
}
.homePage__button:hover {
  transform: scale(1.2);
  margin-top: 300px;
}
.button__span {
  transition: 0.2s;
  margin-bottom: 40px;
}
.homePage__button:hover .button__span {
  margin-top: -125px;
}
@keyframes text {
  from {
    bottom: 400px;
    transform: translate(170%, 400%);
    transform-origin: left center;
  }
  to {
    bottom: 400px;
    transform: translate(-170%, -400%);
    transform-origin: right center;
  }
}

.area {
  background: #ffffff;
  width: 100%;
  height: 100vh;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: #21b8ed;
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}
@media (max-width: 1350px) {
  .homePage__header {
    font-size: 5rem;
  }
  .homePage__button {
    margin-top: 220px;
    width: 200px;
    height: 100px;
    padding: 25px;
  }
  .button__img {
    width: 80px;
  }
  .homePage__button:hover .button__span {
    margin-top: -110px;
  }
}

@media (max-width: 950px) {
  .homePage__header {
    font-size: 3.8rem;
  }
  .homePage__button {
    padding: 15px;
    font-size: 2.3rem;
    width: 150px;
    height: 70px;
  }
  .homePage__button:hover .button__span {
    margin-top: -95px;
  }
}
@media (max-width: 390px) {
  .homePage__header {
    font-size: 3rem;
  }
  .homePage__button {
    margin-top: 240px;
  }
}
